<template>
    <div class='jf_rule'>
        <div class='jf_rule_head'>
            <img src="../../assets/image/积分页背景.png" alt="" class='headbg'>
            <div class='back' @click='back()'>
                <img src="../../assets/image/返回.png" alt="">
            </div>
            <div class='totalJF'>{{selfCount}}</div>
            <div class='totalTitle'>总积分</div>
            <div class='JFranking' @click.stop='enterJFranking()'>
                <img src="../../assets/image/JF排行.png" alt="">
                <div class='ranking'>{{ranking}}</div>
            </div>
            <div class='enterButton'>
                <div class='JFexplain' @click.stop='enterJFexplain()'>积分说明</div>
                <div class='JFdetail' @click.stop='enterJFdetail()'>积分明细</div>
                <div class='JFpaiming' @click.stop='enterJFranking()'>积分排名</div>
            </div>
        </div>
        <div class='jfrule_contain'>
            <div class='jfChange'>
                <img src="../../assets/image/积分改变提醒.png" alt="">
                <div class='change_remind'>积分规则已于{{changeTime}}更新</div>
            </div>
            <div class='contain_top'>
                <div class='JFRule'>积分规则</div>
                <div class='today'>
                    今日累计<div class='todayJF'>{{todayScore}}</div>积分
                </div>
            </div>
            <div  class='jfList'  v-for="item in jfList" :key='item.code'>
                <div class='listLeft'>
                    <div class='listName'>{{item.title}}</div>
                    <div class='listRule'>{{item.completeScore}}分/{{item.content}}</div>
                    <div class='listProgress'>
                        <div class='progressContainer'>
                            <div class="progress" :style="{width:(item.gs/item.topScore)*100+'%'}" v-if='item.gs <= item.topScore'></div>
                            <div class="progress" :style="{width:(item.topScore/item.topScore)*100+'%'}" v-if='item.gs > item.topScore'></div>
                        </div>
                        <div class='getScore'>已获{{item.gs}}分/上限{{item.topScore}}分</div>
                    </div>
                </div>
                <div class='completed' v-if='item.gs/item.topScore >= 1'>
                    已完成
                </div>
                <div class='unfinished' v-if='item.gs/item.topScore < 1 && item.classify == 1'>
                    未完成
                </div>
                <div class='unfinished' v-if='item.gs/item.topScore < 1 && item.classify == 2' @click.stop='enterDataList(2)'>
                    去学习
                </div>
                <div class='unfinished' v-if='item.gs/item.topScore < 1 && item.classify == 3' @click.stop='enterDataList(1)'>
                    去答题
                </div>
                <div class='unfinished' v-if='item.gs/item.topScore < 1 && item.classify == 4' @click.stop='enterStudy()'>
                    去模拟
                </div>
                <div class='unfinished' v-if='item.gs/item.topScore < 1 && item.classify == 5' @click.stop='enterTextlist()'>
                    去考试
                </div>
                <div class='unfinished' v-if='item.gs/item.topScore < 1 && item.classify == 6' @click.stop='enterStudy()'>
                    去观看
                </div>
                <div class='unfinished' v-if='item.gs/item.topScore < 1 && item.classify == 7' @click.stop='enterNewsList()'>
                    去阅读
                </div>
                <div class='completed' v-if='item.classify == 8 || item.classify == 9 || item.classify == 10 || item.classify == 11'>
                    研发中
                </div>
            </div>
        </div>
        <!-- 圆角弹窗 -->
        <van-popup
          v-model="showpopup"
          round
          position="bottom"
        >
        <van-tree-select
          :items="items"
          :active-id.sync="activeId"
          :main-active-index.sync="activeIndex"
          @click-nav='changeLeft'
          @click-item='changeRight'
        />
        <div class='selectEnd'>
          <div style='color:#6E747E' @click.stop='cancel'>取消</div>
          <span></span>
          <div style='color:#5794F6' @click.stop='sure'>确定</div>
        </div>
        </van-popup>
        <!-- 加载页面 -->
        <Loading :dialogVisible="verDialogVisible"></Loading>
    </div>
</template>
<script>
import Loading from './../components/loading'
import API from '@/api/api'
import $ from 'jquery'
export default {
  name: 'index',
  components: {
    Loading
  },
  data () {
    return {
      enterRouter: 0,
      items: [],
      activeId: '',
      activeIndex: 0,
      showpopup: false,
      specialcode: '0',
      jfList: [], // 积分规则列表
      todayScore: 0, // 今日总积分
      selfCount: '', // 当前积分
      ranking: '', // 积分排名
      changeTime: '', // 修改日期
      verDialogVisible: true, // 加载页
      getindCreditsAndGsCG: false, // 获取积分规则成功
      findHistoryUpdateCG: false, // 获取修改时间成功
      sxLastNumber: 0 // 获取专项下之前看到试题数
    }
  },
  computed: {
    requestSuccess () {
      return this.getindCreditsAndGsCG && this.findHistoryUpdateCG
    }
  },
  created () {
    this.getindCreditsAndGs()
    this.findHistoryUpdate()
    this.items = JSON.parse(JSON.stringify(this.$store.state.spe.speMap))
    this.activeId = this.items[0].children[0].id
  },
  watch: {
    showpopup () {
      if (!this.showpopup) {
        this.activeIndex = 0
        this.activeId = this.items[0].children[0].id
      }
    },
    requestSuccess (val, newval) {
      if (val === true) {
        this.verDialogVisible = false
      } else {

      }
    }
  },
  methods: {
    // 返回上一路由
    back () {
      this.$router.back()
    },
    // 获取修改时间
    findHistoryUpdate () {
      this.findHistoryUpdateCG = false
      API.post('findHistoryUpdate', {
      }, 'json', (data, err) => {
        if (data.success) {
          if (data.data) {
            this.changeTime = this.GLOBAL.changeYear2(data.data)
            $('.jfChange').css('display', 'flex')
          } else {
            $('.jfChange').css('display', 'none')
          }
          this.findHistoryUpdateCG = true
          // console.log(data)
        } else {
        }
      })
    },
    // 获取积分规则数据
    getindCreditsAndGs () {
      this.getindCreditsAndGsCG = false
      API.post('findCreditsAndGs', {
      }, 'json', (data, err) => {
        if (data.success) {
          this.jfList = data.data.creditsAndGs // 积分规则列表
          this.selfCount = data.data.selfCount // 当前积分
          this.ranking = data.data.one.rn // 积分排名
          for (var i = 0; i < this.jfList.length; i++) { // 今日总积分
            this.todayScore += Number(this.jfList[i].gs)
          };
          this.getindCreditsAndGsCG = true
        } else {
          alert(data.status)
        }
      })
    },
    // 进入积分说明
    enterJFexplain () {
      this.$router.push('/jf_explain')
    },
    // 进入积分明细
    enterJFdetail () {
      this.$router.push('/jf_detail')
    },
    // 进入积分排名
    enterJFranking () {
      this.$router.push('/jf_ranking')
    },
    // 去学习，去观看,去模拟进入学习界面
    enterStudy () {
      this.$router.push('/data_list')
    },
    // 去答题
    enterDataList (index) {
      this.enterRouter = index
      this.showpopup = true
      // this.$router.push('/data_list')
    },
    // 点击分类选择左侧
    changeLeft (index) {
      this.activeIndex = index
      this.activeId = this.items[index].children[0].id
    },
    // 点击分类选择右侧
    changeRight (data) {
      this.activeId = data.id
    },
    // 点击取消
    cancel () {
      this.showpopup = false
    },
    // 点击确定
    sure () {
      if (this.enterRouter === 1) {
        API.post(
          'findExeNumber',
          {
            specialType: this.activeId
          },
          'json',
          (data, err) => {
            if (data.success) {
              this.sxLastNumber = data.data.exerciseNo
              this.$router.push({ // 进入随机练习
                name: 'random_practice',
                params: {
                  specialCode: this.activeId,
                  practiceStatus: 1,
                  numberMes: this.sxLastNumber
                }
              })
            } else {
              this.$toast(data.msg)
            }
          }
        )
      } else {
        localStorage.setItem('dataListSpecialCode', this.activeId)
        this.$router.push({
          name: 'book_list'
        })
      }
    },
    // 去答题，去模拟进入主页
    enterItembank () {
      this.$router.push('/item_bank')
    },
    // 去考试进入考试界面
    enterTextlist () {
      this.$router.push('/text_list_wait')
    },
    // 去阅读进入主页
    enterNewsList () {
      this.$router.push('/data_list')
    }
  }
}
</script>
<style scoped>
    .jf_rule{
        width:100%;
        height:100%;
    }
    .jf_rule_head{
        position: fixed;
        top:0;
        left:0;
        z-index:99;
        width:100%;
        height:3.18rem;
    }
    .jf_rule_head .headbg{
        width:100%;
        height:3.18rem;
    }
    .back{
        width:0.68rem;
        height:0.94rem;
        display: flex;
        justify-content: center;
        align-items: center;
        position: fixed;
        top:0;
        left:0;
        z-index: 1000;
    }
    .back img{
        width:0.2rem;
        height:0.36rem;
        display: block;
    }
    .totalJF{
        position: absolute;
        top:0.76rem;
        left:0;
        width:100%;
        display: flex;
        justify-content: center;
        align-items: center;
        font-size:0.66rem;
        color:#fff;
    }
    .totalTitle{
        position: absolute;
        top:1.54rem;
        left:0;
        width:100%;
        display: flex;
        justify-content: center;
        align-items: center;
        font-size:0.3rem;
        color:#fff;
    }
    .JFranking{
        position: absolute;
        top:0.69rem;
        right:0.35rem;
        display: flex;
        align-items: flex-end;
    }
    .JFranking img{
        width:0.27rem;
        height:0.25rem;
        display: block;
        margin-bottom: 0.05rem;
        margin-right:0.15rem;
    }
    .JFranking .ranking{
        min-width:0.739rem;
        height:0.42rem;
        font-size:0.42rem;
        font-family:Source Han Sans CN;
        font-weight:400;
        line-height:0.42rem;
        color:rgba(255,255,255,1);
        opacity:0.73;
    }
    .enterButton{
        position: absolute;
        bottom:0.24rem;
        left:0;
        padding:0 0.75rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
        width:100%;
    }
    .enterButton div{
        width:1.48rem;
        height:0.55rem;
        border:1px solid #fff;
        display: flex;
        justify-content: center;
        /* align-items: center; */
        color:#fff;
        font-size:0.26rem;
        border-radius: 0.35rem;
        box-sizing:border-box;
        /* padding-top:0.02rem; */
        line-height: 0.57rem;
    }
    .jfrule_contain{
        width:100%;
        height:calc(100vh - 3.18rem);
        overflow: scroll;
        -webkit-overflow-scrolling: touch;
        margin-top:3.18rem;
        padding:0 0.35rem;
        background: #fff;
    }
    .jfChange{
        margin-top:0.23rem;
        display: flex;
        align-items: center;
        justify-content: center;
    }
    .jfChange img{
        width:0.29rem;
        height:0.29rem;
        display: block;
    }
    .jfChange .change_remind{
        font-size:0.24rem;
        color:#C2CBD6;
        margin-left:0.1rem;
        padding-top:0.04rem;
    }
    .contain_top{
        width:100%;
        height:0.92rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    .contain_top .JFRule{
        font-size:0.34rem;
        color:#4E4E4E;
    }
    .contain_top .today{
        font-size:0.28rem;
        color:#6E747E;
        display: flex;
        align-items: center;
        letter-spacing: 0.01rem;
    }
    .contain_top .today .todayJF{
        color:#5794F6;
        font-size:0.4rem;
        display: flex;
        align-items: center;
        justify-content: center;
        padding-bottom:0.055rem;
    }
    .jfrule_contain .jfList{
        width:100%;
        height:1.65rem;
        border-top:0.012rem solid #F3F3F3;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding:0.3rem 0;
    }
    .jfrule_contain .jfList .listLeft{
        height:100%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
    }
    .jfrule_contain .jfList .listLeft .listName{
        font-size:0.3rem;
        color:#6E747E;
    }
    .jfrule_contain .jfList .listLeft .listRule{
        font-size:0.28rem;
        color:#6E747E;
    }
    .jfrule_contain .jfList .listLeft .listProgress{
        display: flex;
        align-items: center;

    }
    .jfrule_contain .jfList .listLeft .listProgress .progressContainer{
        width:1.42rem;
        height:0.1rem;
        background:#F5F6F8;
        border-radius:0.05rem;
        position: relative;
    }
    .jfrule_contain .jfList .listLeft .listProgress .progressContainer .progress{
        height:0.1rem;
        border-radius:0.05rem;
        background: linear-gradient(180deg,rgba(92, 165, 248, 1) 0%,rgba(129, 178, 255, 1) 100%);
    }
    .listProgress .getScore{
        font-size:0.26rem;
        color:#9FA7AF;
        margin-left:0.28rem;
    }
    .jfList .completed{
        width:1.24rem;
        height:0.53rem;
        display: flex;
        align-items: center;
        justify-content: center;
        background: #F2F5F9;
        color:#9FA7AF;
        font-size:0.25rem;
        border-radius: 0.53rem;
        padding-top:0.02rem;
    }
    .jfList .unfinished{
        width:1.24rem;
        height:0.53rem;
        display: flex;
        align-items: center;
        justify-content: center;
        background: rgba(241,247,255,1);
        color:#5794F6;
        font-size:0.25rem;
        border-radius: 0.53rem;
        padding-top:0.02rem;
    }
    .selectEnd{
      height:1rem;
      width:100%;
      display: flex;
      align-items: center;
      justify-content: space-between;
      border-top:0.02rem solid #E7ECF4;
    }
    .selectEnd div{
      width:calc((100vw - 0.02rem) / 2);
      text-align: center;
      font-size:0.34rem;
      letter-spacing: 0.02rem;
    }
    .selectEnd span{
      width:0.02rem;
      height:0.42rem;
      background: #E7ECF4;
    }
</style>
<style lang="stylus">
  .jf_rule .van-popup{
    max-height:8.73rem !important;
  }
  .jf_rule .van-tree-select{
    height:7.72rem !important;
  }
  .jf_rule .van-sidebar-item--select{
    border-color: #5794F6
    color:#5794F6;
  }
  .jf_rule .van-tree-select__item--active{
    color:#5794F6
  }
  .jf_rule .van-tree-select__nav-item--active{
    color:#5794F6;
    border-color: #5794F6;
  }
  .jf_rule .van-tree-select__item{
    font-weight:400
  }
</style>



